import GeneralUtils from './GeneralUtils'

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
const log = GeneralUtils.createScopedLog('OnboardingRedirect')

function initOnboardingListener() {
  window.addEventListener('stayfocusd-start-onboarding', () => {
    const port = chrome.runtime.connect({ name: 'startOnboardingAfterInstall' })
    port.postMessage({
      message: 'start-onboarding',
    })
  })
}

try {
  initOnboardingListener()
  log('initOnboardingListener', 'Success', null, 'INFO')
} catch (err) {
  log('initOnboardingListener', 'Fail', err, 'ERROR')
}
